import { useCallback, useRef, useState } from "react";
import { Button } from "../../components/button";
import emailjs from "@emailjs/browser";
import "./style.scss";
import { roomsCells } from "./data";
import { stylesCells } from "./data";

export const QuestionnairePage = () => {
  const [isOpenInput, setIsOpenInput] = useState(false);
  const formRef = useRef<HTMLFormElement>(null);

  const toggleInput = () => {
    setIsOpenInput(!isOpenInput);
  };

  const sendEmail = (e: any) => {
    e.preventDefault();

    emailjs.sendForm(
      "service_cfbbb8x",
      "template_m81z2nd",
      e.target,
      "_G1NABxE2QnPGrTHI"
    );
    resetForm();
    setIsOpenInput(false);
  };
  const resetForm = useCallback(() => {
    if (formRef?.current?.reset) {
      formRef?.current?.reset();
    }
  }, []);
  return (
    <div>
      <div className="questionnaire-page">
        <h2 className="questionnaire-page__title">–ê–Ω–∫–µ—Ç–∞ —Å –ø–æ–∂–µ–ª–∞–Ω–∏—è–º–∏ </h2>
        <div className="questionnaire-page__list">
          <ul>
            <li>–í—ã –º–æ–∂–µ—Ç–µ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –∏–ª–∏ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∞–Ω—Ç–µ–∫—É –¥–æ –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏–∏</li>
            <li>–ú—ã –±—É–¥–µ–º –±–æ–ª–µ–µ –∏–Ω—Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω—ã</li>
            <li>–í—ã –º–æ–∂–µ—Ç–µ –º–µ–Ω—è—Ç—å —Å–≤–æ–∏ –ø–æ–∂–µ–ª–∞–Ω–∏—è –ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏</li>
          </ul>
        </div>
        <div className="questionnaire-page__form-wrapper">
          <form
            onSubmit={sendEmail}
            className="questionnaire-page__form"
            id="questionnaire"
            ref={formRef}
          >
            <input placeholder="E - mail" type="email" required name="email" />
            <div className="questionnaire-page__block">
              <span>
                1. –ö–æ—Ä–æ—Ç–∫–æ –æ–ø–∏—à–∏—Ç–µ —Å–æ—Å—Ç–∞–≤ —Å–µ–º—å–∏, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –ø—Ä–æ–∂–∏–≤–∞—Ç—å –≤
                –∫–≤–∞—Ä—Ç–∏—Ä–µ /–¥–æ–º–µ, –≤–∫–ª—é—á–∞—è –¥–æ–º–∞—à–Ω–∏—Ö –∂–∏–≤–æ—Ç–Ω—ã—Ö:
              </span>
              <textarea name="family" id="family" placeholder="–°–æ—Å—Ç–∞–≤ —Å–µ–º—å–∏" />
            </div>
            <span>2. –í –ø–ª–∞–Ω–∏—Ä–æ–≤–∫–µ –ø—Ä–µ–¥—É—Å–º–æ—Ç—Ä–µ–Ω—ã –∫–æ–º–Ω–∞—Ç—ã:</span>
            <div className="questionnaire-page__rooms">
              {roomsCells.map((roomsCell) => {
                return (
                  <div className="questionnaire-page__row">
                    <input
                      type="checkbox"
                      id={roomsCell.name}
                      name={roomsCell.name}
                      value={roomsCell.value}
                    />
                    <label htmlFor={roomsCell.name}>{roomsCell.value}</label>
                  </div>
                );
              })}
              <div className="questionnaire-page__row">
                <input type="checkbox" onClick={toggleInput} id="othercheck" />
                <label htmlFor="othercheck">–î—Ä—É–≥–æ–µ </label>
                {isOpenInput && (
                  <textarea placeholder="–ú–æ–π –æ—Ç–≤–µ—Ç" name="other" id="other" />
                )}
              </div>
            </div>
            <div className="questionnaire-page__block">
              <span>
                3. –ü–æ–¥—Ä–æ–±–Ω–æ –æ–ø–∏—à–∏—Ç–µ –ø–µ—Ä–µ–ø–ª–∞–Ω–∏—Ä–æ–≤–∫—É, –∫–æ—Ç–æ—Ä—É—é —Ö–æ—Ç–µ–ª–æ—Å—å –±—ã —Å–¥–µ–ª–∞—Ç—å
                (–µ—Å–ª–∏ –ø–µ—Ä–µ–ø–ª–∞–Ω–∏—Ä–æ–≤–∫–∏ –Ω–µ –±—É–¥–µ—Ç, –æ—Å—Ç–∞–≤—å—Ç–µ –≥—Ä–∞—Ñ—É –ø—É—Å—Ç–æ–π):
              </span>
              <textarea name="planning" placeholder="–ú–æ–π –æ—Ç–≤–µ—Ç" />
            </div>
            <span>4. –°—Ç–∏–ª–µ–≤—ã–µ –ø—Ä–µ–¥–ø–æ—á—Ç–µ–Ω–∏—è:</span>
            <div className="questionnaire-page__styles">
              {stylesCells.map((stylesCell) => {
                return (
                  <div className="questionnaire-page__row">
                    <input
                      type="checkbox"
                      name={stylesCell.name}
                      value={stylesCell.value}
                      id={stylesCell.name}
                    />
                    <label htmlFor={stylesCell.name}>{stylesCell.value}</label>
                  </div>
                );
              })}
            </div>
            <span className="questionnaire-page__note">
              *–ü–æ–∂–µ–ª–∞–Ω–∏—è –∏ –ø—Ä–µ–¥–ø–æ—á—Ç–µ–Ω–∏—è —Ä–∞—Å—Å—á–∏—Ç–∞–Ω—ã –¥–ª—è –∂–∏–ª–æ–≥–æ –ø–æ–º–µ—â–µ–Ω–∏—è. –í
              —Å–ª—É—á–∞–µ –µ—Å–ª–∏ –ø–æ–º–µ—â–µ–Ω–∏–µ –æ–±—â–µ—Å—Ç–≤–µ–Ω–Ω–æ–µ, –ø–æ–∂–µ–ª–∞–Ω–∏—è –æ–±—Å—É–∂–¥–∞—é—Ç—Å—è –Ω–∞
              –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏–∏.
            </span>
          </form>
        </div>
        <div className="questionnaire-page__actions">
          <Button onClick={resetForm}>–û—á–∏—Å—Ç–∏—Ç—å —Ñ–æ—Ä–º—É</Button>
          <Button form="questionnaire">–û—Ç–ø—Ä–∞–≤–∏—Ç—å –∞–Ω–∫–µ—Ç—É</Button>
        </div>
      </div>
    </div>
  );
};
